@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');


*{
padding: 0;
margin: 0;
font-family: "Inter", sans-serif;
font-optical-sizing: auto;
}

.search-icon{
    width: 20px;
    height: 20px;
    margin-top: 5px;

}
.input-container{
display: flex;
}
body{
    background-color: #2f3640;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
    width: 350px;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgb(0, 0,0,0,0.1);  
position: relative;
}
.input-container{
    width: 100%;
    display: flex;
    border: 2px solid #70e6f3;
    border-radius:10px;
    overflow: hidden;
}
.cityinput{
    flex: 1;
    outline: none;
    height: 30px;
    border: none;
    margin: 10px 10px;
}
.search-iconcontainer{
    padding: 10px;
    height: 30px;
    cursor: pointer;
}
.image{
    text-align: center;
    margin-bottom: 10px;   
}
.image img{
    width: 180px;
    height: 180px;
}
.temp,.location,.country{
    margin-top: 5px;
    font-size: 35px;
    text-transform: uppercase;
    text-align: center;
    color: #333;
}
.temp{
    font-weight: 900;
}

.location{
    color: #ffbc00;
    font-weight: 400;
    font-size: 40px;
}
.country{
    font-size: 18px;
    color: #888;
font-weight: 500;
}
.cord{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}
.cord div{
text-align: center;
font-size: 18px;

}
.cord div p:nth-child(2){
    font-weight: bold;
}
.waveimg,.windimg{ 
    width:50px;
    height: 50px;
} 
.dataconatiner{
    display: flex;
justify-content: space-between;
}
.data{
    text-align: center;
}
.text{
    font-weight: 900;
    color: #888;
}
.humiedity-prcent,.wind-speed{
    color: #888;
    font-weight: 699;
}
.cpyright{
    text-align: center;
    margin-top: 10px;
    font-size: 20px;
    color: #888;
}
.karthi{
    color: #ffbc00;
    font-weight: 700;
}
.loading-message,.error,.city-not-found{
    margin-top: 10px;
    color: #888;
    font-size: 25px;
    font-weight: 400;
    text-align: center;
}